import React, {Component} from 'react';
//import { Badge } from 'reactstrap';
import ModalExample from  './Modal'
import {Comentarios} from './Comentarios'
export class Comentar extends Component{
  constructor(props) {
    super(props);
    this.state=
    {
      comentario: new ComentarioDetalle()
    }
  }
  render(){
    return (
      <div className="section">
        <h1>Comentarios </h1>
        <ModalExample buttonLabel="Comentar" titulo="Agregar comentario" Action={Comentar.Summit} />
        <Comentarios />
      </div>);
  }
  static Summit(form){
    //const data = new FormData(document.getElementById('formulario'));
    fetch('/Usuarios/SetComentarios', {
       method: 'post',
       body: form
    })
    .then(function(response) {
       if(response.ok) {
           return response.text()
       } else {
           throw "Error en la llamada Ajax";
       }

    })
    .then(function(texto) {
       console.log(texto);
    })
    .catch(function(err) {
       console.log(err);
    });
  }
}
class ComentarioDetalle {
  nombre:'';
  email:'';
  comentario:'';
  constructor(nombre,email,comentario) {
    this.nombre=nombre;
    this.email=email;
    this.comentario=comentario;
  }
}
